<template>
  <section class="issues" v-if="overviewList">
    <h2>安全信息</h2>
    <div>
      <div class="issues-highest">
        <p>
          <i class="overview-warning icon-red">
            &#xe638;</i>
          <span>{{overviewList.high}}</span>
        </p>
        <p>高危漏洞</p>
      </div>
      <div class="issues-highest">
        <p>
          <i class="overview-warning icon-orange">
            &#xe638;</i>
          <span>{{overviewList.medium}}</span>
        </p>
        <p>中间漏洞</p>
      </div>
      <div class="issues-highest">
        <p><i class="overview-warning icon-yellow">
          &#xe638;</i>
          <span>{{overviewList.low}}</span>
        </p>
        <p>低危漏洞</p>
      </div>
      <div class="issues-highest">
        <p><i class="overview-warning icon-blue">
          &#xe638;</i>
          <span>{{overviewList.negligible}}</span>
        </p>
        <p>轻微漏洞</p>
      </div>
      <div class="issues-highest">
        <p><i class="overview-warning icon-black">
          &#xe638;</i>
          <span>{{overviewList.unknown}}</span>
        </p>
        <p>未知漏洞</p>
      </div>
    </div>
  </section>
</template>

<script>
  export default {
    name: 'level',
    props: ['overviewList']
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  div[class^="issues"]
    display inline-block
    width: 215px
    height 75px
    margin 48px 0 40px 0
    line-height: 30px
    p
      text-align: left
      color: #808080
      font-size: 1.4em
      i
        display: inline-block
        margin: 0 20px 0 0
      span
        color: #4D4D4D
        font-size: 2.0em
        font-weight: 600
    p:last-child
      margin: 0px 0 0 58px

  .split-line
    color: #E6E6E6

  .bg-purple
    background: #F8F8F8

  .grid-content
    height: 145px;

  .media-right-title
    font-size: 1.4em
    color: #5F5F5F
    font-weight 600
    padding: 20px 35px
    display: inline-block

  .media-right-time, .media-right-size
    color: #5F5F5F
    font-size: 12px
    float: right
    padding: 20px 18px 0 0

  .media-right-size
    margin: 0 20px 0 0

  i[class^="icon-overview"]
    margin: -3px 8px 0 0
    font-size: 2.3em

  div[class^="overview-wrapper"]
    display: inline-block
    margin: 20px 25px 60px 18px
    width: 18%
    p
      text-align: center
      color: #808080
      font-size: 1.2em
      line-height: 26px
      i
        display: inline-block
        margin: -3px 10px 0 0
      span
        color: #4D4D4D
        font-size: 1.8em
        font-weight: 600
    p:last-child
      margin: 0 0 0 50px

  .overview-warning
    font-size: 2.3em

  .icon-red
    color: #FF0000

  .icon-orange
    color: #F27935

  .icon-yellow
    color: #F9BF3B

  .icon-blue
    color: #08B1D5

  .icon-black
    color: #1A1A1A
</style>



