<template>
  <div>
    <section class="msg" v-if="overviewList">
      <div>
        <div class="msg-wrapper">
          <p><i>*</i>储存库/标签：</p>
          <span>{{overviewList.image}}</span>
        </div>
        <div class="msg-wrapper">
          <p><i>*</i>注册表：</p>
          <span>Docker Hub</span>
        </div>
        <div class="msg-wrapper">
          <p><i>*</i>镜像创建时间：</p>
          <span>{{overviewList.created_at|timeTrans}}</span>
        </div>
        <div class="msg-wrapper">
          <p><i>*</i>最新扫描时间：</p>
          <span>{{overviewList.updated_at|timeTrans}}</span>
        </div>
        <div class="msg-wrapper">
          <p><i>*</i>操作系统 / 版本 ：</p>
          <span>{{overviewList.namespace}}</span>
        </div>
      </div>
    </section>
    <hr>
    <level :overviewList="overviewList"></level>
  </div>
</template>

<script type="text/ecmascript-6">
  import * as scanerApi from '@/api/transactionScaner2'
  import level from './level'

  export default {
    components: {
      level
    },
    data() {
      return {
        overviewList: ''
      }
    },
    created() {
      // 拉取index页面的数据
      let data = this.$route.query
      scanerApi.fetchImageDetail(data).then(res => {
        console.log(res.data.data)
        this.overviewList = res.data.data
      })
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .msg
    text-align: left
    column-count: 2
    margin: 40px 0 40px 0
    .msg-wrapper
      color: #333333
      margin: 0 0 34px 0
    p
      font-size: 1.3em
      display inline-block
      width: 32%
      max-width: 40%
      i
        color: #FF0808
        font-size: 1.3em
        margin: 0 4px 0 0
    span
      font-size: 1.4em
      color: #525252

  .dockerImage-id
    word-break: break-all
</style>



